(function () {
    'use strict';
    var angular = require('angular');

    var dipErrorComp = angular.module('app').component('dipError', {
        template: require('./dip-error.component.html'),
        controller: ['$anchorScroll', 'analyticService', 'appState','journeyService', function ($anchorScroll, analyticService, appState,journeyService) {
            var vm = this;
            vm.$onInit = function () {
                vm.isStaffJourney = appState.isStaffJourney();
                console.log("PREVIOUS");
                vm.aapsIssue = journeyService.getAapsIssue();
                console.log("AAPS FLAG ", vm.aapsIssue);
                analyticService.trackPageLoad(analyticService.pageList.dipConnectivityIssue);
                appState.clearAll();
                $anchorScroll();
            };
            vm.Close = function () {
                window.location.href = "http://www.hsbc.co.in/";
            }
        }]
    });

    module.exports = dipErrorComp.name;
})();


